* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --main-font: "Noto Sans", sans-serif;
  --body-font: "Assistant", sans-serif;
  --main-bg-color: #2c5364;
  --main-gradient-bg-color: linear-gradient(to left, #2c5364, #203a43);
  --bg-color: linear-gradient(to left, #203a43, #2c5364);
  --graident-bg-color: linear-gradient(to left, #44a08d, #093637);
  --alternativ-bg-color: linear-gradient(#37adb2, #329ca0);
  --alternativ-bg-color2: linear-gradient(20deg, #22c1c3, #fdbb2d);
  --message-gradient: linear-gradient(70deg, #203a4396, #2c536465);
  --message-background: #6aa4bd2c;
}

html {
  font-size: 14px;
  font-family: var(--main-font);
  height: 100%;
}

body {
  flex-direction: column;
  height: 100%;
  display: flex;
}

h1, h2, h3, h4, h5, h6 {
  text-align: center;
  margin: .5em 0;
}

h1 {
  margin: 1.5em 0 .5em;
  font-size: 1.3rem;
}

h2 {
  margin: 1em 0 .5em;
  font-size: 1.1rem;
}

h3 {
  margin-top: 0;
}

p {
  font-family: var(--body-font);
  margin-bottom: 7px;
}

p:last-child {
  margin-bottom: 0;
}

img {
  max-width: 100%;
}

a {
  color: #0a5254;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.hide {
  display: none !important;
}

input {
  font-family: var(--main-font);
  padding: 3px;
}

#search input {
  font-family: var(--main-font);
  width: 250px;
}

.searchIcon {
  position: relative;
}

.searchIcon:after {
  content: "";
  color: #0f2027;
  margin-top: -24px;
  font-family: FontAwesome;
  position: absolute;
  right: 6px;
}

button {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #0000001a;
  border-radius: .35rem;
  padding: 5px 10px;
}

button:hover {
  background-color: #e7e7e7;
  border: 1px solid #3d3d3d1a;
}

header {
  background: linear-gradient(to left, #44a08d, #093637);
  background: var(--graident-bg-color);
  background: var(--main-gradient-bg-color);
  color: #fff;
  width: 100%;
  position: fixed;
}

header a {
  color: #fff;
}

.main-nav {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding: 1em;
  font-size: 1.2rem;
  font-weight: 600;
  display: flex;
}

.menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.menu > li {
  margin: 0 1rem;
  overflow: hidden;
}

.menu > li a {
  font-weight: 400;
  text-decoration: none;
}

.menu > li a:hover {
  text-decoration: underline;
}

ul a .active {
  font-weight: 700;
}

.search {
  justify-content: space-between;
  display: flex;
}

#topMenuUser {
  width: 200px;
  font-size: 1.1em;
}

#topMenuUser a:before {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: auto;
  font-variant: normal;
  color: #fff;
  content: "";
  content: "";
  content: "";
  content: "";
  content: "";
  margin: 0 7px 0 5px;
  font-family: "Font Awesome 6 Free";
  font-size: 1.1em;
  font-weight: 900;
  line-height: 1;
  display: inline-block;
}

.pageWrapper {
  justify-content: center;
  padding-top: 50px;
  display: flex;
}

main {
  border-radius: 5px;
  width: 80%;
  min-height: 80vh;
  margin: 0 auto;
  box-shadow: 0 6px 24px #0000000d, 0 0 0 1px #00000014;
}

.mainContent {
  width: 98%;
  margin: 0 auto;
}

.startWrapper {
  text-align: center;
  max-width: 700px;
  margin: 0 auto;
  padding: 15px;
}

aside {
  font-family: var(--main-font);
  background-color: #c3cbdc;
  background-image: linear-gradient(147deg, #c3dcdc 0%, #edf1f4 74%);
  width: 20vw;
  max-width: 220px;
  padding: 20px;
  box-shadow: 0 6px 24px #0000000d, 0 0 0 1px #00000014;
}

.mainFooter {
  background-color: #fff;
  background: var(--graident-bg-color);
  color: #fff;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  margin-top: auto;
  padding: 1.4em;
  display: flex;
}

.copyright {
  font-size: .7em;
}

.mainFooter ul {
  flex-direction: row;
  align-items: flex-end;
  gap: 2px 15px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.mainFooter a, .mainFooter a:hover {
  color: #fff;
}

@media only screen and (width <= 601px) {
  #search {
    width: 50%;
  }

  #search input {
    width: 150px;
  }

  #topMenuUser {
    width: 20%;
    font-size: .9rem;
    display: none;
  }

  .pageWrapper {
    flex-flow: column-reverse wrap;
  }

  aside {
    justify-content: flex-start;
    align-items: flex-start;
    width: 100vw;
    max-width: 100%;
    display: flex;
  }

  aside img {
    max-height: 30vw;
    margin: 0 auto;
  }

  .userMenu {
    border-right: 1px solid #ccc;
    flex-direction: column;
    width: 50%;
    display: flex;
  }

  .users {
    margin: 0 auto;
    padding-top: 0;
    padding-left: 15px;
    border-top: none !important;
    width: 40% !important;
  }
}
